yout-color1);\n  border-top: 1px solid var(--jp-border-color2);\n}\n\n/* Input field styles */\n.jp-llm-ext-input-field {\n  width: 100%;\n  min-height: 80px; /* Increased min height */\n  max-height: 250px;\n  padding: 12px 16px;\n  border: 1px solid var(--jp-border-color2);\n  border-radius: 10px;\n  background-color: var(--jp-layout-color1);\n  color: var(--jp-ui-font-color1);\n  font-size: 14px;\n  resize: vertical;\n  transition: all 0.2s ease;\n  line-height: 1.5;\n  font-family: var(--jp-ui-font-family);\n}\n\n.jp-llm-ext-input-field:focus {\n  outline: none;\n  border-color: var(--jp-brand-color1);\n  box-shadow: 0 0 0 1px var(--jp-brand-color1);\n}\n\n/* Control container styles */\n.jp-llm-ext-controls-container {\n  display: flex;\n  align-items: center;\n  padding: 4px 8px;\n  margin-bottom: 8px;\n}\n\n/* Toggle container styles */\n.jp-llm-ext-toggle-container {\n  display: flex;\n  align-items: center;\n  flex-shrink: 0;\n}\n\n/* Markdown toggle checkbox */\n#markdown-toggle {\n  margin-right: 5px;\n  cursor: pointer;\n  width: 16px;\n  height: 16px;\n  accent-color: var(--jp-brand-color1);\n}\n\n/* Markdown toggle label */\n#markdown-toggle + label {\n  font-size: 13px;\n  color: var(--jp-ui-font-color2);\n  cursor: pointer;\n}\n\n#markdown-toggle + label:hover {\n  color: var(--jp-brand-color1);\n}\n\n/* Action buttons container - Closer to toggle */\n.jp-llm-ext-action-buttons-container {\n  display: flex;\n  gap: 6px;\n  align-items: center;\n  margin-left: 8px; /* Reduced margin */\n  flex-shrink: 0;\n}\n\n/* Control buttons in the action-buttons-container */\n.jp-Button.jp-llm-ext-action-button {\n  min-width: 28px;\n  width: 28px;\n  height: 28px;\n  padding: 0;\n  display: inline-flex;\n  align-items: center;\n  justify-content: center;\n  border-radius: 6px;\n  font-size: 14px;\n  background-color: var(--jp-layout-color2);\n  color: var(--jp-ui-font-color1);\n  border: 1px solid var(--jp-border-color2);\n  cursor: pointer;\n  transition: all 0